'use strict';

/**
* Return the last `n` Unicode code points of a string.
*
* @module @stdlib/string-base-last-code-point
*
* @example
* var last = require( '@stdlib/string-base-last-code-point' );
*
* var out = last( 'Hello World', 1 );
* // returns 'd'
*
* out = last( 'अनुच्छेद', 1 );
* // returns 'द';
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
